<template>
    <div>
        <top-nav title="拼团活动列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p> 1、 这里是为商品添加拼团活动</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="团购ID">
                        <el-input v-model="searchForm.groupbuy_id" placeholder="团购ID"></el-input>
                    </el-form-item>

                    <el-form-item label="商品ID">
                        <el-input v-model="searchForm.goods_id" placeholder="商品ID"></el-input>
                    </el-form-item>

                    <!--<el-form-item label="关键词">-->
                        <!--<el-input v-model="searchForm.keywords" placeholder="关键词"></el-input>-->
                    <!--</el-form-item>-->

                    <el-form-item label="是否上线中">
                        <el-select v-model="searchForm.is_online" placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="已下线" :value="0"></el-option>
                            <el-option label="上线中" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="添加日期">
                        <date-between v-model="searchForm.adddate"></date-between>
                    </el-form-item>

                    <div style="padding-left:20px;">
                        <el-button size="mini" type="primary" @click="getList(1)">搜索</el-button>
                        <el-button size="mini" type="text" @click="searchForm.groupbuy_id='',searchForm.goods_id='',
                        searchForm.is_online=-999,searchForm.adddate=[],getList(1)">取消搜索</el-button>
                        <router-link to="/marketing/groupbuy/create">
                            <el-button size="mini" type="danger">添加拼团</el-button>
                        </router-link>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" size="small" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>

                    <el-table-column prop="groupbuy_id" label="团购ID" width="60"></el-table-column>

                    <el-table-column prop="goods_id" label="编码/ID" width="150">
                        <template slot-scope="scope">
                            <p>商品ID：<span>{{scope.row.goods_id}}</span></p>
                            <p>商品编码：<span>{{scope.row.goods_sn}}</span></p>
                        </template>
                    </el-table-column>

                    <el-table-column label="商品名称" width="240">
                        <template slot-scope="scope">
                            <div class="ellipsis-02">
                                {{scope.row.goods_name}}
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="缩略图" width="80">
                        <template slot-scope="scope">
                            <img width="60" :src="scope.row.photo"/>
                        </template>
                    </el-table-column>
                    <el-table-column prop="need_num" label="成团需人数"  width="80"></el-table-column>

                    <el-table-column prop="success_num" label="已成团人数" width="80"></el-table-column>

                    <el-table-column label="成团周期" width="80">
                        <template slot-scope="scope">
                            <p>{{scope.row.end_time}} 小时</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="最低团购价" width="200">
                        <template slot-scope="scope">
                            <p>注册会员：<b style="color: #D94854">￥ {{scope.row.groupbuy_price}}</b> </p>
                            <p>PLUS会员：<b style="color: #D94854">￥ {{scope.row.vip_groupbuy_price}}</b> </p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="num" label="活动数量" width="80"></el-table-column>

                    <el-table-column prop="orderby" label="排序" width="80"></el-table-column>
                    <el-table-column label="操作日期/IP" width="200">
                        <template slot-scope="scope">
                            <p>日期：{{scope.row.add_time}}</p>
                            <p>IP：{{scope.row.add_ip}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column prop="goods_sn" label="活动开启" width="80">
                        <template slot-scope="scope">
                            <el-switch @change="setOnline(scope)" :value="scope.row.is_online" :inactive-value="0" :active-value="1" ></el-switch>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                            <el-col :span="8">
                                <router-link :to="'/marketing/groupbuy/edit/' + scope.row.groupbuy_id">
                                    <el-button size="mini" type="text">编辑</el-button>
                                </router-link>
                            </el-col>
                            <el-col :span="8" style="text-align: center">
                                <el-popover placement="top" v-model="scope.row.is_delete">
                                    <p style="margin-bottom: 4px">确定删除并释放产品管理吗？</p>
                                    <div style="margin: 0">
                                        <el-button :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消</el-button>
                                        <el-button :loading="loading" type="primary" size="mini" @click="del(scope)">确定</el-button>
                                    </div>
                                    <el-button type="text" size="mini" slot="reference">删除</el-button>
                                </el-popover>
                            </el-col>
                            <el-col :span="8">
                                <router-link :to="'/marketing/groupbuy/join/' + scope.row.groupbuy_id">
                                    <el-button size="mini" type="text">查看参团</el-button>
                                </router-link>
                            </el-col>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import DateBetween from "@/components/DateBetween";

    export default {
        name: 'goodsAttrIndex',
        components: {TopNav,DateBetween},
        data() {
            return {
                //整体菊花
                loading: false,
                //列表数据
                datas: [],
                //搜索数据
                searchForm: {
                    groupbuy_id: '',
                    goods_id: '',
                    keywords: '',
                    adddate: [],
                    is_online: -999,
                },
            }
        },

        created() {
            this.getList();
        },
        methods: {


            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.marketing.groupbuy/index", {
                    appToken: this.$store.getters.appToken,
                    groupbuy_id: this.searchForm.groupbuy_id,
                    keywords: this.searchForm.keywords,
                    goods_id: this.searchForm.goods_id,
                    bg_add_time: this.searchForm.adddate[0],
                    end_add_time: this.searchForm.adddate[1],
                    is_online: this.searchForm.is_online,
                    page: page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },



            handleSelectionChange(val) {
                let Ids = [];
                for (let i in val) {
                    Ids.push(i);
                }
                this.groupbuyIds = Ids;
            },



            setOnline(scope){
                this.loading = true;
                let is_online = 0;
                if (scope.row.is_online == 0) {
                    is_online = 1;
                }
                api.post("/shop/admin.marketing.groupbuy/online", {
                    appToken: this.$store.getters.appToken,
                    groupbuy_id: scope.row.groupbuy_id,
                    is_online: is_online,
                }).then(res => {
                    this.getList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            del(scope) {
                this.loading = true;
                api.post("/shop/admin.marketing.groupbuy/del", {
                    appToken: this.$store.getters.appToken,
                    groupbuy_id : scope.row.groupbuy_id,
                }).then(res => {
                    this.getList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },
        }
    }
</script>

